<template>
  <v-dialog persistent v-model="value" max-width="600px">
    <v-card>
      <v-card-title>
        <span class="text-h6">{{
          isEdit ? "Update Staff" : "Create Staff"
        }}</span>
      </v-card-title>
      <v-card-text>
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-row>
            <v-col cols="6">
              <v-text-field
                v-model="staff.firstName"
                :rules="[(v) => !!v || 'First name is required']"
                label="First name"
                required
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="staff.lastName"
                :rules="[(v) => !!v || 'Last name is required']"
                label="Last name"
                required
                filled
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6">
              <v-text-field
                v-model="staff.branch"
                :rules="[(v) => !!v || 'Branch is required']"
                label="Branch"
                required
                filled
              ></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="staff.designation"
                :rules="[(v) => !!v || 'Designation is required']"
                label="Designation"
                required
                filled
              ></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <v-text-field
                v-model="staff.address"
                :rules="[(v) => !!v || 'Address is required']"
                label="Address"
                required
                filled
              ></v-text-field>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="error" class="mr-4" @click="close"> Cancel </v-btn>
        <v-btn
          :disabled="!valid"
          color="primary"
          class="mr-4"
          @click.prevent="validate"
        >
          {{ isEdit ? "Update" : "Create" }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  data() {
    return {
      valid: true,
      staff: {
        firstName: "",
        lastName: "",
        branch: "",
        designation: "",
        address: "",
      },
      isEdit: false,
    };
  },
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    selectedStaff: {
      type: Object,
      default: null,
    },
  },
  created() {
    if (this.selectedStaff) {
      this.isEdit = true;
      this.staff.firstName = this.selectedStaff.firstName;
      this.staff.lastName = this.selectedStaff.lastName;
      this.staff.branch = this.selectedStaff.branch;
      this.staff.designation = this.selectedStaff.designation;
      this.staff.address = this.selectedStaff.address;
    } else {
      this.clear();
    }
  },
  methods: {
    validate() {
      this.$refs.form.validate();
      if (this.valid && this.isEdit) {
        this.$emit("update-staff", this.staff, this.selectedStaff._id);
      }
      if (this.valid && !this.isEdit) {
        this.$emit("create-staff", this.staff);
      }
    },
    close() {
      this.$emit("input", !this.value);
    },
    clear() {
      this.staff.firstName = "";
      this.staff.lastName = "";
      this.staff.branch = "";
      this.staff.designation = "";
      this.staff.address = "";
    },
  },
};
</script>

