      this.isAddEditPopupOpen = true;
    },
    createStaff(staff) {
      this.staffs.push(staff);
      this.snackbar = true;
      this.snackbarText = "Staff created successfully";
      this.isAddEditPopupOpen = false;
    },
    updateStaff(staff, _id) {
      this.staffs.forEach((element, index) => {
        if (element._id === _id) {
          Object.assign(this.staffs[index], staff);
          this.snackbar = true;
          this.snackbarText = "Staff updated successfully";
        }
      });
      this.isAddEditPopupOpen = false;
    },
    deleteSelectedStaff() {
      this.staffs = this.staffs.filter(
        (staff) => staff._id !== this.selectedStaff._id
      );
      this.selectedStaff = null;
      this.isDeleteConfimationPopupOpen = false;
      this.snackbar = true;
      this.snackbarText = "Staff deleted successfully";
    },
  },
};
